* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

.app{
    min-height: 100vh;
    background-color: #39445a;
    color: #ffffff;
    padding-top: 130px;
    padding-bottom: 70px;
}

.pageTitle{
    text-transform: uppercase;
    display: flex;
    justify-content: center;
    font-family: Montserrat, sans-serif;
    font-size: 2vw;
    padding: 4px;
    border-radius: 50px;
    color: white;
}

@media (max-width: 1000px) {
    .pageTitle{
        font-size: 6.4vw;
    }
}

